import CustomSelect from './customSelect';
import phoneMask from './phone-mask.js'
import validate from './validate.js'
import { forEach, onlyNumber, maxValue } from './helpers.js'

validate('#form-resume')

onlyNumber(document.querySelectorAll('[data-field="number"]'))
maxValue(document.querySelectorAll('input[type="number"]'))
CustomSelect(document.querySelectorAll('.select'))

forEach(document.querySelectorAll('[data-field="phone"]'), el => {
  phoneMask(el)
});

forEach(document.querySelectorAll('.b-file'), item => {
  const inputEl = item.querySelector('input[type="file"]')
  const attachedEl = item.querySelector('.b-file__attached')
  const attachedCloseEl = attachedEl.querySelector('.b-file__attached-close')
  const attachedTextEl = attachedEl.querySelector('.b-file__attached-text')
  const allowedTextEl = item.querySelector('.b-file__allowed-text')
  inputEl.addEventListener('change', () => {
    if (inputEl.files[0].size > 10485760) {
      item.classList.add('has-error')
    } else {
      item.classList.remove('has-error')
      attachedEl.classList.add('is-active')
      attachedTextEl.textContent = inputEl.files[0].name
      allowedTextEl.style.display = 'none'
    }
  })
  attachedCloseEl.addEventListener('click', () => {
    attachedEl.classList.remove('is-active')
    attachedTextEl.textContent = ''
    inputEl.value = ''
    allowedTextEl.style.display = 'block'
  })
});
