import Bouncer from 'formbouncerjs'

export default function(form) {

    const validateConfig = {
      messages: {
        missingValue: {
          default: '    '
        },
        patternMismatch: {
          email: '  email ',
          tel: '  '
        },
        wrongLength: {
          under: '     {minLength} .'
        }
      }
    };

    const bouncer = new Bouncer(form, {
      errorClass: 'help-text error-message',
      messages: Object.assign(validateConfig.messages, {}),
      // disableSubmit: true,
      emitEvents: true
    });

    return bouncer
    
}