import SimpleBar from 'simplebar';
import { break_md } from '../../js/constants';

;(function() {
  const vacancyContent = document.querySelector('.b-vacancy__content')
  if (!vacancyContent) return;
  if (window.innerWidth <= break_md) return

  new SimpleBar(vacancyContent, {
    // forceVisible: true,
    scrollbarMinSize: 30,
    autoHide: false,
    forceVisible: 'y' 
  });
}());
