;(function() {
  const header = document.querySelector('.header')
  if (!header) return;

  if (window.scrollY > header.offsetHeight) {
    document.body.classList.add('is-header-white')
  }
  window.addEventListener('scroll', () => {
    if (window.scrollY > header.offsetHeight) {
      document.body.classList.add('is-header-white')
    } else {
      document.body.classList.remove('is-header-white')
    }
  })    
}());
