import { Swiper, Navigation, Pagination, EffectFade, Thumbs } from 'swiper/dist/js/swiper.esm.js'

Swiper.use([Navigation, Pagination, EffectFade, Thumbs]);

const sliderText = new Swiper('.sliderMainText', {
    speed: 500,
    slidesPerView: 1,
    freeMode: true,
    watchSlidesVisibility: true,
    watchSlidesProgress: true,
  }
)

const sliderTitle = new Swiper('.sliderMainTitle', {
    speed: 500,
    slidesPerView: 1,
    freeMode: true,
    watchSlidesVisibility: true,
    watchSlidesProgress: true,
    direction: 'vertical',
    pagination: {
        el: '.sliderMainWrapper .swiper-pagination',
        type: 'custom',
        renderCustom: function (swiper, current, total) {
            return '0'+current + '&nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp;' + '0'+total;
        }
    },

    navigation: {
      nextEl: '.sliderMainWrapper .swiper-button-next',
      prevEl: '.sliderMainWrapper .swiper-button-prev',
    },

    thumbs: {
      swiper: sliderText
    },
  }
)

;(function () {
  const videoEl = document.querySelector('.MainImgVideo video');
  if(!videoEl) return;

  const setVideoSize = () => {
    videoEl.style.height = 'auto'
    videoEl.style.width = document.body.offsetWidth + 'px'
    if(videoEl.offsetHeight < window.innerHeight) {
      videoEl.style.height = window.innerHeight + 'px'
      videoEl.style.width = 'auto'
    }
  }

  videoEl.addEventListener('playing', () => {
    videoEl.parentNode.classList.add('is-active')
    // setVideoSize()
  });

  // videoEl.addEventListener('page-ready', () => {
  //   videoEl.currentTime = 0;
  // });

  // window.addEventListener("resize", function () {
  //   setTimeout(function () {
  //     setVideoSize()
  //   }, 50)
  // });
}());
