import { Swiper, Navigation } from 'swiper/dist/js/swiper.esm.js'
import { forEach } from '../../js/helpers'

Swiper.use([Navigation]);


forEach(document.querySelectorAll('.companyItemSm'), item => {
  const hidden = item.querySelector('.companyItemSm__hidden')
  item.style.setProperty('--max-height', hidden.scrollHeight + 'px')
});

if (window.innerWidth < 992) {

  new Swiper('.companies__items_top', {
      slidesPerView: 3,
      spaceBetween: 20,
      centeredSlides: true,
      // autoHeight: true,
      loop: true,

      navigation: {
        nextEl: '.companies .swiper-button-next',
        prevEl: '.companies .swiper-button-prev',
      },

      // Responsive breakpoints
      breakpoints: {
        767: {
          slidesPerView: 1,
          spaceBetween: 0,
        },
        
      }
    }
  )

}

setTimeout(() => {
  forEach(document.querySelectorAll('.companyItem'), item => {
    const hidden = item.querySelector('.companyItem__hidden')
    const addClass = () => {item.classList.add('hover')}
    item.addEventListener('mouseenter', addClass)
    // item.addEventListener('touchstart', addClass)
    item.addEventListener('mouseleave', () => {
      setTimeout(() => {
        item.classList.remove('hover')
      }, 500)
    })
    item.style.setProperty('--max-height', hidden.scrollHeight + 'px')
  });
}, 500)
