import MicroModal from 'micromodal'
import CustomSelect from "../../js/customSelect";

(function () {
    // init() //after Ajax success
    function ready(fn) {
        if (document.readyState != 'loading') {
            fn();
        } else {
            document.addEventListener('DOMContentLoaded', fn);
        }
    }

    ready(function () {
        var select = document.querySelector('#obl-select')
        if (!select) return;
        select.addEventListener('change', () => {
            console.log('test change');
            var index = document.querySelector("#obl-select").selectedIndex;
            var obl_id = document.querySelector("#obl-select").options[index].dataset.id;

            var data = "OBL=" + obl_id

            var request = new XMLHttpRequest();
            request.open('POST', '/bitrix/templates/apteka/vacancy.php', true);
            request.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded; charset=UTF-8');
            request.onload = function () {
                if (this.status >= 200 && this.status < 400) {
                    // Success!
                    var resp = this.response;
                    document.querySelector('.vacancy.container').innerHTML = resp;
                    CustomSelect(document.querySelectorAll('.modal .select'))
                }
            };
            request.send(data);
        })
    });
}());



MicroModal.init({
    awaitOpenAnimation: true,
    awaitCloseAnimation: true,
});
