import { forEach } from '../../js/helpers'

;(function() {
    const popup = document.querySelector('.popup-mobile')
    if(!popup) return;

    const overlay = document.querySelector('.overlay')
    const popupLink = document.querySelector('.popup-mobile-link')
    const closeEls = document.querySelectorAll('[data-dismiss="popup"]')
    const close = () => {
        popup.classList.remove('is-active')
        overlay.classList.remove('is-active')
        popupLink.classList.remove('is-active')
    }

    popupLink.addEventListener('click', () => {
        popup.classList.add('is-active')
        overlay.classList.add('is-active')
        popupLink.classList.add('is-active')
    })

    forEach(closeEls, el => {
        el.addEventListener('click', () => {
            close()
        })
    })

    forEach(document.querySelectorAll('.nav-mobile a'), el => {
        el.addEventListener('click', () => {
            close()
        })
    })

}());
